import React, { useState, useEffect } from 'react';
import productDatabase from '../ProductDatabase/ProductDatabase';
import axios from 'axios';

const LiveDeals = () => {
  
    const [localApi, setLocalApi] = useState([]);
    const [localApiLoading, setLocalApiLoading] = useState(true);
    const [webApi, setWebApi] = useState([]);
    const [webApiLoading, setWebApiLoading] = useState(true);
    const [error, setError] = useState('');
    
    const WEBAPIURL = "https://api.escuelajs.co/api/v1/products";

    const selectedProducts = [1, 4, 7, 9, 13,15];

    const selectedProductsData = localApi.filter(product => selectedProducts.includes(product.id));

useEffect (()=>{
    const fetchAPiData = async () => {
        try{
            setTimeout(()=>{
                setLocalApi(productDatabase);
                setLocalApiLoading(false);
            },2000)   
        }catch(error){
            console.error('Error fetching data:', error );
        }   
    };
    fetchAPiData();
},[])

useEffect (()=>{
    const fetchAPiData = async () =>{
        try{
            
            const WebApiData = await axios.get(WEBAPIURL);
            setWebApi(WebApiData.data);
        }catch(error){
            console.error('Error fetching data:', error );
        }finally{
            setWebApiLoading(false);
        } 
    };
    fetchAPiData();
    

},[WEBAPIURL])

useEffect (() => {
    if(localApiLoading || webApiLoading){
        console.log('Local Database loading....', localApi);
        console.log('Web  Database loading....', localApi);

    }else{
        console.log('Local API Database:', localApi); 
        console.log('Web API Database:', webApi);
    }
  }, [localApiLoading, webApiLoading, localApi, webApi]);

const [viewCounts, setViewCounts] = useState({});

  useEffect (()=>{
    selectedProductsData.forEach(productId =>{
        setViewCounts(lastCount =>({
            ...lastCount,
            [productId]:(lastCount[productId] || 0) + 1,
        }));
    });
  },[localApiLoading, webApiLoading])

  return (
    <>
      <div className="live-deals-wrapper" md={6} xs={12}>
        <div className="dot-container d-flex" id="container-new-dot">
          <div className="dot"></div>
          <div className="pulse"></div>
          <div className="deals-head">
            <h2 className='text-bold primary-font'>Live Deals</h2>
          </div>
        </div>
        <div className="deals-list-wrapper">
            {(localApiLoading || webApiLoading) ?(
                <div className='loading-container'>
                    <img src='https://icons8.com/preloaders/preloaders/1475/Skateboarding.gif'/>
                </div>
            ):(
            <ul className="product-list d-flex flex-column">
                {selectedProductsData.map((product)=>(
                    <li key={product.id} className='flex-row flex-start d-flex live-deal-product-section'>
                        <div className='live-deal-image-section'>
                            <img src={product.image} alt="" className="live-deal-img full-width"/>
                        </div>
                        <div className="live-deals-details">
                            <p className="secondary-font product-name">{product.productName}</p>
                            <del>{product.originalPrice}</del>
                            <span><strong className='primary-font discountedPrice'>{product.discountedPrice}</strong></span>
                        </div>
                        <div className='view-count text-center'>
                            <p>Views: <br />
                                <span>
                                    {viewCounts[product.id] || 0}
                                </span>
                            </p>
                        </div>
                    </li>
                ))}
            </ul>
            )}
        </div>
      </div>
    </>
  );
};

export default LiveDeals;

         
            